import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { PopupComponent } from '../popup/popup.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { UtilService } from '../util.service';

@Component({
  selector: 'app-employee-list',
  templateUrl: './employee-list.component.html',
  styleUrls: ['./employee-list.component.css']
})
export class EmployeeListComponent implements OnInit {

  constructor(private httpClient: HttpClient, private modalService: NgbModal, private util: UtilService) { }

  employeesList: any = [];
  searchText = "";
  itemsInPage = "10";

  ngOnInit(): void {
    this.getEmployees();
    this.util.announceEmployeeAddedSource.subscribe(obj => {
      this.getEmployees();
    })
  }

  getEmployees() {
    this.httpClient.get("/api/employees").subscribe((response: any) => {
      this.employeesList = response;
    }, err => {
      console.log("Error in getting /api/employees: ", err);
    })
  }

  showAddNewEmployee() {
    this.modalService.open(PopupComponent);
  }
  deleteEmployee(item) {
    this.httpClient.delete(`/api/employees/${item._id}`).subscribe((response: any) => {
      this.getEmployees();
    })
  }
  editEmployee(item) {
    const dpModalInstance = this.modalService.open(PopupComponent);
    dpModalInstance.componentInstance.employeeObj = item;
  }
}
