import { Component, OnInit, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { HttpClient } from '@angular/common/http';
import { UtilService } from '../util.service';

@Component({
  selector: 'app-popup',
  templateUrl: './popup.component.html',
  styleUrls: ['./popup.component.css']
})
export class PopupComponent implements OnInit {
  @Input() employeeObj = null;
  emptyObj = {
    firstName: "",
    lastName: "",
    dob: "",
    gender: "male",
    role: "",
    contactNumber: "",
    address: "",
    basicAllowance: "",
    fixedAllowance: ""
  }
  mappingObj: any = {};

  constructor(public activeModal: NgbActiveModal, private httpClient: HttpClient, private util: UtilService) { }

  ngOnInit(): void {
    if (this.employeeObj) {
      this.mappingObj = JSON.parse(JSON.stringify(this.employeeObj));
    } else {
      this.mappingObj = JSON.parse(JSON.stringify(this.emptyObj));
    }
  }

  addNewEmployee() {
    let method = "post", url = `/api/employees`;
    if (this.mappingObj._id) {
      method = "put";
      url = `/api/employees/${this.mappingObj._id}`;
    }
    this.httpClient[method](url, this.mappingObj).subscribe((response: any) => {
      this.activeModal.dismiss('Cross click');
      this.util.announceEmployeeAdded(this.mappingObj);
    });
  }
}
